#calculator #box {
  background-color: #37474f;
  border: 5px solid #263238;
  border-radius: 5px;
  width: 243px;
  height: 304px;
  padding: 6px;
  margin: auto;
  margin-top: 250px;
  margin-bottom: 50px;
  margin-bottom: 130px;
  font-size: 20px;
}

#calculator input {
  width: 238px;
  height: 50px;
  /* margin-top: 70px; */
  /* margin-bottom: 50px; */
  /* margin-right: 50px; */
  font-size: 30px;
  text-align: end;
  margin-right: 1px;
}

#calculator .container {
  width: 410px;
  height: 47px;
  margin-top: 3px;
  /* margin-bottom: 10px; */
  /* margin-right:5px; */
}
#calculator button:hover {
  background-color: #37474f;
}

#calculator button:active {
  background-color: #37474f;
  color: white;
  box-shadow: 0 5px #263238;
  transform: translateY(4px);
}

#calculator button {
  background-color: #cfd8dc;

  width: 61px;
  height: 50px;
  border: 2px solid #37474f;
  font-size: 30px;
}

#calculator .eq {
  width: 122px;
  margin-top: 1px;
  margin-right: 10px;
}

#calculator .container:last-child button:not(.eq),
#calculator .container:first-child button:not(.eq) {
  position: relative;
  top: 12px;
}
