#signupForm {
  background-color: #37474f;
  width: 222px;
  height: 258px;
  padding: 61px;
  padding-left: 18px;
  padding-bottom: 62px;
  margin: auto;
  margin-top: 170px;
  border: 2px solid black;
  border-radius: 30px 20px;
}
#signupForm .WELCOME {
  width: 200px;
  height: 90px;
  padding-right: 70px;
  margin-right: 50px;
  margin-top: auto;
  /* margin-right: 50px; */
  padding-right: 60px;
  font-size: 18px;
}

#signupForm input {
  width: 253px;
  height: 15px;
  padding: 10px;
  padding-bottom: 10px;
  margin-bottom: 10px;
  margin-right: 100px;
  margin-left: none;
  font-size: large;
  text-align: center;
  border-radius: 30px 20px;
  color: #263238;

  /* margin-right: 50px; */
}

#signupForm .required::after {
  content: "*";
  color: black;
}
#signupForm button {
  width: 265px;
  height: 40px;
  background-color: #263238;
  border-color: 5px black;
  color: black;
  cursor: pointer;

  font-size: 21px;
  font-weight: 300px;
  text-align: center;
  /* transform: translateY(4px); */

  /* border:none; */
}

#signupForm .button:hover {
  background-color: #546e7a;
}
#signupForm .button:active {
  background-color: #37474f;
  box-shadow: 0 5px #666;
  transform: translateY(1px);
  transition: 4s;
}
