
  #page .box {

    display: inline-block;
    margin: 10px;
    background-color: #37474f;
    width: 225px;
    height: 330px;
    padding: 20px;
    /* margin: auto; */
    /* margin-top: 150px; */
    border: 2px solid #263238;
    border-radius: 3px solid #37474f;
    box-shadow: 5px 5px 5px 5px #37474f;
    /* transition: 2s; */


  }
  
  #page .button {
    width: 50px;
    height: 20px;
    background-color: #263238;
    margin-left: 80px;
    margin-top: 2px;
    border: none;
  }
  
  #page button:hover {
    background-color: black;
    color: white;
    transition: 2s;
  }
  
  
  
  