
 #detailcard .card {
    background-color: #37474f;
    display: inline-block;
    width: 220px;
    height: 330px;
    padding: 20px;
    margin-right: 50px;
    margin-left: 45px;
    margin-bottom: 25px;
    position: absolute;
    top:40;
  
    /* margin: auto;. */
    /* margin-top: 150px; */
    border: 2px solid #263238;
    border-radius: 6px;
    border-radius: 3px solid #37474f;
    box-shadow: 5px 5px 5px 5px #37474f;
  }
  #detailcard .button {
    width: 50px;
    height: 20px;
    background-color: #37474f;
    margin-left: 80px;
    margin-top: 2px;
    border: none;
    /* color: white; */
  }
  #detailcard button:hover {
    background-color: black;
    color: white;
    transition: 2s;
  }

