#pagination .pagecontent{
color:aliceblue;
}
.pag__table {
    font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 100%;
    border: 1px solid black;
}


.pag__table th{
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color: black;
    color: white;
    text-transform: uppercase;
}

.buttons button.active{
    background-color: black;
    color:white;
}